import { Link } from "react-router-dom"
import { RPTooltip } from "@rp/react/web/common/base-components"
import { useBackOrNavigate } from "@rp/react/web/common/hooks"
import { Header } from "@rp/react/web/common/layout"
import { Box, IconButton, Typography } from "@rp/react/web/mui/core"
import { ArrowBack } from "@rp/react/web/mui/icons"
import { WATCHLISTS_ROUTE } from "@rp/react/web/router"

type Props = {
  info?: { text: string; tooltip?: string } | null
}

const WatchlistDashboardHeader = ({ info }: Props) => {
  const navigate = useBackOrNavigate()

  return (
    <Header
      sx={{ "& .MuiContainer-root": { maxWidth: "100%" } }}
      leftColumns={6}
      rightColumns={6}
      rightContent={
        info ? (
          <RPTooltip title={info.tooltip}>
            <Typography sx={{ cursor: "default", color: "text.secondary" }} variant="body2">
              {info.text}
            </Typography>
          </RPTooltip>
        ) : null
      }
      leftContent={
        <Box
          sx={{
            display: "flex",
            alignItems: "center",
            gap: 1,
            minWidth: 0,
          }}
        >
          <Link
            to={""}
            onClick={ev => {
              ev.preventDefault()
              navigate(WATCHLISTS_ROUTE)
            }}
          >
            <IconButton size="small">
              <ArrowBack />
            </IconButton>
          </Link>
        </Box>
      }
    />
  )
}

export { WatchlistDashboardHeader }
